.pagination-enhanced {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 0 1rem 0;
  padding: 0;
  list-style: none;
  font-family: "Helvetica Neue", Helvetica, Roboto, Arial, sans-serif;
}

.pagination-enhanced li {
  margin: 0 0.25rem;
}

.pagination-enhanced a,
.pagination-enhanced button {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 2rem;
  height: 2rem;
  padding: 0 0.5rem;
  border: 1px solid #cacaca;
  border-radius: 0;
  background-color: #fefefe;
  color: #0a0a0a;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.25s ease;
}

.pagination-enhanced a:hover,
.pagination-enhanced button:hover {
  background-color: #e6e6e6;
  color: hsl(0, 0%, -6.0784313725%);
  border-color: rgb(176.5, 176.5, 176.5);
}

.pagination-enhanced .current {
  background-color: #1779ba;
  border-color: #1779ba;
  color: #fefefe;
  font-weight: bold;
}

.pagination-enhanced .disabled {
  background-color: #e6e6e6;
  border-color: #cacaca;
  color: #cacaca;
  cursor: not-allowed;
}

.pagination-enhanced .disabled:hover {
  background-color: #e6e6e6;
  color: #cacaca;
  border-color: #cacaca;
}

.pagination-enhanced .ellipsis::after {
  content: "…";
  display: block;
  text-align: center;
  line-height: 2rem;
  color: #8a8a8a;
}

.pagination-enhanced .pagination-previous a::before,
.pagination-enhanced .pagination-previous.disabled::before {
  content: "«";
  margin-right: 0.25rem;
}

.pagination-enhanced .pagination-next a::after,
.pagination-enhanced .pagination-next.disabled::after {
  content: "»";
  margin-left: 0.25rem;
}

@media print, screen and (min-width: 40em) {
  .pagination-enhanced a,
  .pagination-enhanced button,
  .pagination-enhanced .current,
  .pagination-enhanced .disabled,
  .pagination-enhanced .ellipsis::after {
    min-width: 2.5rem;
    height: 2.5rem;
    padding: 0 0.75rem;
    font-size: 1.125rem;
  }
  .pagination-enhanced .ellipsis::after {
    line-height: 2.5rem;
  }
}
